body {
  margin: 0;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
}

.logo {
  display: block;
}

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  height: 60px;
}

.nav__item {
  display: flex;
  justify-content: space-between;
  margin-left: 20px;
}

.nav__item:first-child {
  margin: 0;
}

.nav__link {
  display: flex;
  position: relative;
  color: black;
  padding-top: 20px;
  padding-bottom: 20px;
  align-items: center;
  text-decoration: none;
  font-family: Roboto , sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 12px;
  line-height: 14px;
  text-transform: uppercase;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active::after {
  content: "";
  position: absolute;
  height: 4px;
  width: 100%;
  bottom: 0;
  right: 0;
  background-color: #00acdc;
  border-radius: 8px;
}
